<template>
  <UserNavComponent>
    <div class="container">
      <div class="row justify-content-center">
        <!-- {{ remainingCourses }} -->
        <h1>UserRecommendation {{ userData }}</h1>
        <div class="card-body">
          <form @submit.prevent="recommend">
            <div class="form-group row">
              <label for="domain" class="col-md-4 col-form-label text-md-right">Domain: </label>
              <div class="col-md-6">
                <b-dropdown id="domain"  v-model="rec.domain" text="Select Domain" class="m-md-2">
                  <b-dropdown-item @click="selectDomain('Data science')">Data science</b-dropdown-item>
                  <b-dropdown-item @click="selectDomain('Programming')">Programming</b-dropdown-item>
                <!-- also use mix of subjects -->
                </b-dropdown>
              </div>
            </div>
            <div class="form-group row">
              <label for="hours" class="col-md-4 col-form-label text-md-right">No.of hours per week:</label>
              <div class="col-md-6">
                <input id="hours" type="number" class="form-control" name="number" v-model="rec.hours"
                  required />
              </div>
            </div>
            <div class="form-group row">
              <label for="count" class="col-md-4 col-form-label text-md-right">Max subjects :</label>
              <div class="col-md-6">
                <input id="count" type="number" class="form-control" name="number" v-model="rec.count"
                  required />
              </div>
            </div>
            <div class="form-group row mb-0">
              <div class="col-md-6 offset-md-4">
                <button type="submit" class="btn btn-primary">Generate Recommendation</button>
              </div>
            </div>
          </form>
        </div>

      </div>
    </div>
  </UserNavComponent>
</template>

<script>
import { mapGetters } from "vuex";
import axios from 'axios';

export default {
  name: "UserRecommendation",
  data() {
    return {
      rec: {
        degree:false,
        hours: 0,
        domain: null,
        count:0,
      },
    };
  },
  methods: {
    recommend() {
      console.log("is it workinggg");
            if (this.rec.domain) { // and number in limit 
              console.log("blaaah",this.remainingCourses);
        axios.post("http://127.0.0.1:8080/user/generateRecommendation", {
          domain: this.rec.domain,
          hours: this.rec.hours,
          degree : this.rec.degree,
          courses : this.remainingCourses,
          count : this.rec.count,
        })
        .then((response) => {
          console.log(response.data.recommendations);
          this.$router.push('/userHomePage');
        })
        .catch((error) => {
          console.error(error);
        });
      } else {
        console.error("Domain not selected");
      }
    },

    selectDomain(domain) {
    this.rec.domain = domain;
  }
  },
  computed: {
    ...mapGetters(["getUserData", 'getRemainingCourses']),
    userData() {
      return this.getUserData[0].onlyDegree;
    },
    remainingCourses() { 
      // console.log(this.getRemainingCourses);
      return this.getRemainingCourses;
    }
  },

};
</script>
<style></style>
