import router from '@/router'
import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    coursesData: "",
    otp: false,
    generateotp: false,
    loggedInUserName: "",
    profilePicture: "",
    userData: {},
    levelCourses: {},
    completedCourses: {},
    remainingCourses:{},
  },
  getters: {
    getCoursesData: (state) => {
      return state.coursesData
    },
    getOTP: (state) => {
      return state.otp
    },
    getUserName: (state) => {
      return state.loggedInUserName
    },
    getUserData: (state) => {
      return state.userData
    },
    getLevelCourses: (state) => {
      return state.levelCourses
    },
    getCompletedCourses: (state) => {
      return state.completedCourses
    },
    getRemainingCourses:(state)=>{
      return state.remainingCourses
    }
  },
  mutations: {
    setCoursesData: (state, data) => {
      state.coursesData = data;
    },
    setOTP: (state, data) => {
      state.otp = data
    },
    setLevelCourses: (state, data) => {
      state.levelCourses = data;
    },
    setUserData: (state, data) => {
      state.userData = data;
    },
    setCompletedCourses: (state, data) => {
      state.completedCourses = data;
    },
    
    setRemainingCourses(state, { all, completed }) {

    const completedSubjects = new Set(completed.map(course => course.subject));
    state.remainingCourses = all.filter(course => !completedSubjects.has(course.name));
    console.log(state.remainingCourses);

    },

    
  },
  actions: {

    async login({ commit }, payload) {
      axios.post("http://127.0.0.1:8080/user/login", payload)
        .then((response) => {
          commit('setgeneratingotp', false);
          console.log(response.data.user_detail);

          if (response.data.role == 'USER') {
            commit('setUserData', response.data.user_detail)
            router.push('/userHomePage')
          }
          else if (response.data.role == 'ADMIN') {
            router.push('/adminHomePage')
          }
        })
        .catch((error) => {
          console.error(error);
        });

    },

    async uploadCSVData({ commit }, payload) {
      axios.post("http://127.0.0.1:8080/admin/uploadData", payload)
        .then((response) => {
          commit('setgeneratingotp', false);
          console.log(response.data);
          router.push('/adminHomePage')
        })
        .catch((error) => {
          console.error(error);
        });
    },

    async submitProfileDetails({ commit }, payload) {
      axios.post("http://127.0.0.1:8080/user/submitProfileDetails", payload)
        .then((response) => {
          commit('setgeneratingotp', false);
          console.log(response.data);
          commit('setUserData', response.data.user_detail)
          router.push('/userHomePage')
        })
        .catch((error) => {
          console.error(error);
        });
    },

    async validateAdmin({ commit }, payload) {
      //returns if the  entered user id is valid or not. 
      // console.log("blaaaah", payload);
      commit('setgeneratingotp', true);

      axios.post("http://127.0.0.1:8080/admin/validatingAdmin", payload)
        .then((response) => {
          console.log(response);
          commit('setgeneratingotp', false);
          commit('setOTP', true);
        })
        .catch((error) => {
          console.error(error.response.data);
        });

    },

    async validateOTP({ commit }, payload) {
      //returns if the OTP entered is successful or not 
      //if the count exceeds 3 times 
      // console.log("async validateOTP");
      axios.get(`http://127.0.0.1:8080/admin/validateotp/${payload.userId}/${payload.otp}`)
        .then((response) => {
          console.log(response);
          commit('setOTP', false);
          router.push('/adminHome')
        })
        .catch((error) => {
          console.error(error.response.data);
        });

    },
    async sendOTP({ commit }, payload) {
      // console.log("send OTP");
      axios.post("http://127.0.0.1:8080/user/sendOTP", payload)
        .then((response) => {
          console.log(response);
          commit('setP');
        })
        .catch((error) => {
          console.error(error.response.data);
        });

    },

    logoutUser({ commit }) {
      localStorage.clear()
      commit('logout')
      router.push('/')
    },

  },
  modules: {
  }
})
